
#my-nav {
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.85);
  box-sizing: border-box;
  padding-top: 30px;
  width: 100%;
  color: #fff;
}
#my-nav a {
    color: #fff;
}
#my-nav .my-nav-inner {
    padding: 0 30px;
}
#my-nav .my-nav-inner .close-btn {
      text-align: right;
      margin-bottom: 50px;
      line-height: 20px;
}
#my-nav .my-nav-inner .close-btn > * {
        vertical-align: middle;
}
#my-nav .my-nav-inner .close-btn a.close-text {
        padding-right: 10px;
}
#my-nav .my-nav-inner ul {
      border-bottom: 1px solid #606060;
}
#my-nav .my-nav-inner ul li {
      margin: 20px 0;
}
#my-nav .my-nav-inner .list5 {
      border: none;
}
#my-nav .my-nav-inner .list5 a {
        line-height: 38px;
        border: 1px solid #fff;
        padding: 0 40px;
        border-radius: 4px;
}



