
@media screen and (min-width: 600px) {
div.home-register > h2 {
    font-size: 30px;
    margin-bottom: 10px;
}
div.home-register > p {
    font-size: 18px;
}
div.home-register .home-register-inner {
    max-width: 460px;
    margin: 0 auto;
}
div.home-desc-item {
    overflow: hidden;
    padding: 5% 0;
    margin: 0;
}
div.home-desc-item > * {
      box-sizing: border-box;
      display: inline-block;
      vertical-align: middle;
      width: 50%;
}
div.home-desc-item .img-bg {
      padding-top: 30%;
      margin-bottom: 0;
}
div.home-desc-item .img-bg-0,
    div.home-desc-item .img-bg-2 {
      transform: translateX(100%);
}
div.home-desc-item .desc-container-0,
    div.home-desc-item .desc-container-2 {
      transform: translateX(-100%);
}
div.home-video .video-content-inner {
    text-align: center;
}
div.home-video .video-content-inner > .title {
      font-size: 38px;
      margin-bottom: 18px;
}
div.home-video .video-content-inner > p {
      font-size: 20px;
      line-height: 1.4;
      margin-bottom: 22px;
}
form.video-register-form input {
    margin: 0;
    width: 48%;
}
form.video-register-form input:first-child {
      margin-right: 4%;
}
form.video-register-form .register-legal {
    margin: 0;
    line-height: 36px;
}
.home-video.is-pc .video-content-inner {
    min-width: 500px;
}
div#my-nav {
    position: absolute;
    width: 35%;
    min-width: 370px;
    min-height: 600px;
    padding: 30px 100px 0 50px;
}
div#my-nav .my-nav-inner {
      box-sizing: border-box;
      width: 280px;
      padding: 0;
}
div#my-nav .my-nav-inner .close-btn {
        margin-bottom: 40px;
}
div#my-nav .my-nav-inner ul li {
        margin: 25px 0;
}
div.footer-nav {
    max-width: none;
    width: 600px;
    margin: 0 auto;
}
div.footer-nav .footer-nav-item {
      width: 25%;
}
.footer-copyright {
    text-align: center;
}
}
@media screen and (min-width: 765px) {
div.home-desc-item .desc-container {
    padding-left: 50px;
    margin-bottom: 0;
}
div.home-desc-item .desc-container > h2 {
      font-size: 30px;
}
div.home-desc-item .desc-container > p {
      font-size: 18px;
}
div.home-video.is-pc {
    position: relative;
    color: #fff;
}
div.home-video.is-pc a {
      color: #fff;
}
div.home-video.is-pc > .video-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.3);
}
div.home-video.is-pc > .video-content .video-content-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
}
div.home-video.is-pc .down-icon {
      display: block;
}
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
body {
  color: #586062;
  font-size: 14px;
  font-family: Hiragino Sans GB, STHeiti, Microsoft Yahei, sans-serif;
  line-height: normal;
}
a {
  color: #586062;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}
li {
  list-style: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.horizontal > * {
  display: inline-block;
}
.vertical-center {
  display: table;
}
.vertical-center > *:first-child {
    display: table-cell;
    vertical-align: middle;
}
.icon-text-hor > * {
  vertical-align: middle;
}
.mobile-1px:after {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  background: #f3f3f3;
  transform: scaleY(0.5);
}
.text-overflow {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.text-overflow-multi-line {
  overflow: hidden;
  text-overflow: ellipsis;
  /* autoprefixer: off*/
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.hide-text {
  text-indent: -99999px;
  vertical-align: bottom;
}
.yinxiang-logo {
  display: inline-block;
  width: 131px;
  height: 45px;
  background: url("/static/img/header/header-sprite.png") no-repeat;
  background-position: -97px 0px;
}
.list-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url("/static/img/header/header-sprite.png") no-repeat;
  background-position: -28px -28px;
}
.close-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url("/static/img/header/header-sprite.png") no-repeat;
  background-position: 2px -6px;
}
html,
body {
  width: 100%;
  height: 100%;
  position: relative;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  transition: transform 1s;
}
.slide-enter,
.slide-leave-to {
  transform: translateX(100%);
}
body {
  overflow-y: scroll;
  min-width: 300px;
}
body img {
    width: 100%;
}



