
.footer-nav {
  padding: 0 20px;
  line-height: 1;
  box-sizing: border-box;
  max-width: 500px;
  font-size: 13px;
}
.footer-nav .footer-nav-item {
    box-sizing: border-box;
    display: inline-block;
    width: 50%;
    vertical-align: top;
    margin-bottom: 30px;
}
.footer-nav .footer-nav-item:nth-child(even) {
      padding-left: 10px;
}
.footer-nav .footer-nav-item .footer-nav-item-title {
      font-weight: bold;
      margin-bottom: 14px;
}
.footer-nav .footer-nav-item .footer-nav-item-item {
      margin-bottom: 12px;
}
.footer-nav .footer-nav-item .footer-nav-item-item a {
        color: #636b6d;
}



