
.home-register {
  background: #49a56d;
  margin-bottom: 50px;
}
.home-register-inner {
  line-height: 1;
  text-align: center;
  overflow: hidden;
  box-sizing: border-box;
  padding: 45px 20px 0 20px;
  color: #fff;
}
.home-register-inner > h2 {
    font-size: 25px;
    margin-bottom: 8px;
}
.home-register-inner > p {
    font-size: 16px;
    margin-bottom: 24px;
}
.home-register-inner .download-btn,
  .home-register-inner .register-btn {
    display: block;
    box-sizing: border-box;
    height: 48px;
    font-size: 16px;
    line-height: 46px;
    color: #fff;
    margin-bottom: 40px;
    border: 1px solid #fff;
    border-radius: 4px;
}
.home-register-inner .download-btn:hover,
    .home-register-inner .register-btn:hover {
      background: #fff;
      color: #49a56d;
}
.home-register-inner .home-register-form .register-legal a {
    color: #fff;
}



