
@keyframes abc {
0% {
    transform: translateY(0);
}
50% {
    transform: translateY(20px);
}
100% {
    transform: translateY(0);
}
}
.home-video.is-pc .video-container {
  position: relative;
}
.home-video.is-pc .video-container .video-img {
    padding-top: 49.375%;
}
.home-video.is-pc .video-container > video {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
}
.home-video.is-pc .down-icon {
  display: none;
  position: absolute;
  left: 50%;
  margin-left: -18px;
  z-index: 99;
  width: 36px;
  height: 40px;
  background: url("/static/img/body/home/down-icon.png") center/contain no-repeat;
  bottom: 40px;
  animation: abc 2s infinite;
}
.home-video .video-container .video-img {
  padding-top: 75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.home-video .video-content {
  background: #f3f3f3;
}
.home-video .video-content-inner {
  max-width: 560px;
  margin: 0 auto;
  padding: 30px 20px 40px 20px;
}
.home-video .video-content-inner > .title {
    margin-bottom: 6px;
    font-size: 20px;
    line-height: 1;
}
.home-video .video-content-inner > p {
    margin-bottom: 18px;
    line-height: 20px;
}
.home-video .video-content-inner .video-btn,
  .home-video .video-content-inner .register-btn {
    line-height: 48px;
    text-align: center;
    border-radius: 4px;
    color: #fff;
    background: #2dbe60;
    width: 100%;
}
.home-video .video-content-inner .video-btn:hover,
    .home-video .video-content-inner .register-btn:hover {
      background: #28a956;
}
.home-video .video-content-inner .video-register-form input {
    height: 34px;
    border: 1px solid;
}
.home-video .video-content-inner .video-login {
    margin-top: 16px;
}
.home-video .video-content-inner .video-login > a {
      text-decoration: underline;
}



